import React, { Component } from 'react';
import logo_bw from './../images/logo_bw.png';
import Description from './description.js';

const styles ={
	
	featureCard :{
				
				textShadow: '1px 1px 1px #fff',
				fontFamily:'sans-serif',
				//color:'#333',
				fontSize:'16px',			
				float: 'left',
				lineHeight: 1.8,		
				textAlign: 'left',
				
				
			},
			
			cardDiv : 'w3-round-xxlarge',
			
		
			
			vertiStyle :{
				letterSpacing:'1.3px',
				fontSize:'14px',
			},
			subtext:{
				fontSize:'14px',
				opacity:0.7,
				marginLeft:'25px',
				marginTop: '-1em',
			}
	
	}

const cards = [];
const lbtns = [];
const btns = ['OVERVIEW','ULTRA-SIMPLE','LIVE DEMO','ONE MONTH FREE','PAY AS YOU GAIN','ROBO EXPERTS\' BACKING',
			'BACK TESTED','HYBRID','CLOUD BASED','TUNABLE','SAFTY WITH TRANSPERANCY','MODEL TRADER'];

			
	

let pageno = 0;

export default class Features extends Component{
	
	constructor( props )
   {
       super(props);
	   this.state ={ cards : 12,pagenm : 0 };
	    this.loadCards();
   }
   
   render(){
	   return(
			
			<div class="w3-cell-row w3-border-bottom ">
				<div class='w3-cell w3-hide-small w3-hide-medium ' style = { { width : '15%' } } >	
				
					{ 
						Object.entries( btns ).map( ( [ key, value ] ) =>{								
							
							return this.lbtn( key, value, this.state.pagenm );
							}
						)
					}					
				
				</div>
				
				<div class='w3-cell w3-display-container ' style={{paddingLeft:'5%', paddingRight:'5%',}}   >
					
					<div class ="w3-container w3-cell-bottom w3-border-top w3-border-black " />					
					{ this.cardList( this.state.pagenm, this.props.nmCards ) }					
								
				<div class ="w3-container w3-cell-bottom w3-large w3-padding" />	
				<div className='w3-display-bottommiddle w3-border-top w3-border-black' style={{fontSize:'12px', width:'85%'}}>	
					
					<button class = "w3-button  w3-left " onClick = { this.prev.bind( this ) }>
						<i class="fa fa-arrow-circle-left fa-2x" aria-hidden="true"></i>
					</button>			
					
					<button class = "w3-button w3-right" onClick = { this.next.bind( this ) } >
						 <i class="fa fa-arrow-circle-right fa-2x" aria-hidden="true"></i>
					</button>	
			
				</div>
				</div>
				
				<div class='w3-cell w3-hide-small w3-hide-medium' style={{width:'15%'}}>
				
					{ 
						Object.entries(btns).map(([key,value])=>{
								return this.rbtn( key, value, this.state.pagenm );
							}
						)
					}
				
				</div>
			</div>  
	   );
   }
	   
	   componentDidMount(){
		  // this.loadButtons();
		  
	   }
	   
   
   lbtn = function( idx, lable, pagenm ){
	   
	   if( idx > 5 ) return ;
	   let clz = 'w3-button w3-block    w3-left-align w3-text-gray' ;  
	   
	   
	   if( idx == 0  ) {
		   clz = ' w3-border-top ' + clz;
	   }
	   if( idx == pagenm  ) {
		   clz = ' w3-border-black w3-border-bottom w3-border-top ' + clz;
	   }
	   else clz = clz + ' w3-border-bottom';
	  
		return (
		   <div 
				className={clz} 			
				style={styles.vertiStyle} 
				onClick={() => { this.jmp(idx).bind(this)} }>{lable}
			</div> );
	 
	   
	  
   }
   
   rbtn = function( idx, lable, pagenm ){
	   
	   if( idx < 6 ) return ;
	   let clz = 'w3-button w3-block   w3-text-gray  w3-right-align';
	  
	   
	   
	  if( idx == 6  ) {
		   clz = ' w3-border-top ' + clz;
	   }
	   if( idx == pagenm  ) {
		   clz = ' w3-border-black w3-border-bottom w3-border-top ' + clz;
	   }
	   else clz = clz + ' w3-border-bottom ';
	  
		return (
			<div 
				className={clz} 			
				style={styles.vertiStyle} 
				onClick={() => { this.jmp(idx).bind(this)} }>{lable}
			</div> );   
	  
		}


   
   cardList = function( pagenm, nmCards ){
	   
	   let items = [];
	
	   for (let index = pagenm ; index < (pagenm +nmCards) ; index++ ) {
		    
			items.push( cards [index]);
		}
		//boxShadow: '0 0 1px 2px  #999',
		return (
			<div class=' w3-center w3-round-large w3-margin-bottom' 
			style={{display: 'flex', justifyContent:'space-around', }} >
			{items}
				
			</div>
		);
	   
   }
   next = function(){
		
		let vlu = this.state.pagenm+this.props.nmCards;
		if( vlu >= this.state.cards )vlu = 0;
	
		this.setState({pagenm:vlu});
	}
	prev = function(){
		
		let vlu = this.state.pagenm-this.props.nmCards;
		if( vlu < 0 )vlu = 0;
		this.setState({pagenm:vlu});
	}
	jmp = function( idx ){

		this.setState({pagenm:parseInt(idx)});
	}
	

	loadCards = function(){
		
		cards.push(
					
					<div class={styles.cardDiv} style={styles.featureCard}>			
				<p  style={{fontSize:'16px',  color:'#000'}}>
					Prameyaka is the cloud-based trading platform designed to provide an ultra-simple day trading solution for those who don't have trading skills, stock market knowledge or extra time to spend. Packed with pre-build strategies and algorithms, this platform consolidates analytical skills with business expert's expertise to provide a complete day-trading solution.
				</p>	
			</div>
		
			);
	   
	  cards.push(
					<div class={styles.cardDiv} style={styles.featureCard}>
				
						
						<p style={{  color:'#000'}}>
							<b>ULTRA-SIMPLE : </b>You just need to approve Prameyaka to manage your trades, then platform will handle the trading on behalf of you. You need not define or code any strategies, instead your trades wil be decided by artificial intelligence of our Robo Expert and expertise of trading experts. 
						</p>
					</div>
				);
			
				cards.push(
					<div class={styles.cardDiv} style={styles.featureCard}>	
						
						
						<p style={{   color:'#000'}}>
							<b>LIVE DEMO : </b>You can have a live online demo without even having a d-mat or trading account. Each day, you can view actual trades triggered for Model Trader with Rs.1 lacks of fund allocation. User-end customization will be disabled for the live demo but can view the real real-time positions created with live market. You can also view the historical snapshots of Live Demos.
							
						</p>	
						<p style={styles.subtext} className='w3-hide'>
							Click on <a href='#' >Login</a> from the menu. Follow simple steps to register for Live Demo by selecting <b><i>Prameyaka</i></b> as your broker.
						</p>
						
						
						
					</div>
						 
				);
			
				cards.push(
					 
					<div class={styles.cardDiv} style={styles.featureCard}>	
						
						<p  style={{   color:'#000'}}>
						<b>ONE MONTH FREE : </b>Prameyaka offerings are free for floating one month ie last month. So new users can have a one-month free trial before subscribing or existing subscriber can avail this offer to discontinue without any dues.
						</p>
						
					</div>
				);
			
				cards.push(
					<div class={styles.cardDiv} style={styles.featureCard}>	
						
						<p style={{  color:'#000'}}>
							<b>PAY AS YOU GAIN : </b>There is no fixed monthly subscription charges and all the offerings are completely free if you don't make a decent profit, equivalent to an annual return of 12%. Otherwise you need to pay <b>25%</b> of your additional net profit as monthly subscription charges. 
						</p>
					</div>	
				);
				
					cards.push(
						<div class={styles.cardDiv} style={styles.featureCard}>	
							
							<p style={{   color:'#000'}}>					
								
								<b>ROBO EXPERTS' BACKING : </b>Robo Expert can drive all the tasks for the user like any day trading expert would do, from picking the right stocks to timing the entry and exit, without any manual intervention. 
								Robo Expert acts as a foundation that guard against the risk of wrong trades due to common human errors of experts like an oversight, absence, quick or emotional actions , especially during volatile market conditions. 
								
							</p>
						</div>
					);
				
					cards.push(
						<div class={styles.cardDiv} style={styles.featureCard}>	
										 
							 <p class=" w3-center " style={{ fontSize:'14px'}}>
								 <i>"Past is the prophet of future", completely known past, can be  the master of  future.</i>
							</p>
							<p  style={{   color:'#000'}}>	
					
								<b>BACK TESTED : </b>The algorithms and strategies has been back-tested, validated, and optimized with more than 16 months of historical Intraday data to produce a best result. 
							</p>
						</div>
					);
				
					cards.push(
						<div class={styles.cardDiv} style={styles.featureCard}>	
											 
							<p style={{   color:'#000'}}>	 
					
								<b>HYBRID : </b>Bussiness and day trading experts continuously hunt for organizational events, business scenarios, and market sentiments that produces predictable price movements. In addition to configuring the stocks manually, they also tune strategic algorithms of Robo expert based on real-time market conditions and business scenarios. 
							</p>
						</div>
					);
				
					cards.push(
						<div class={styles.cardDiv} style={styles.featureCard}>	
										 
							<p style={{ color:'#000'}}>	
					
								<b>CLOUD BASED : </b>Trading as a service is a unique cloud-based solution that frees traders from monitoring, follow up, and the need of a device running throughout market hours. User can access their investment status anywhere through a computer or smartphone. 
								</p>
								<p style={{  color:'333'}}>Users need not install any additional tools or software on their computer, so no additional 3rd party license cost. 
								</p>				
						</div>
					);
				
					cards.push(
						<div class={styles.cardDiv} style={styles.featureCard}>	
										 
							<p style={{ color:'#000'}}>	
					
								<b>TUNABLE : </b>Expert users can also override exit scenarios by setting exit limits and assurance for all or for a  specific stock. 
								<br/>
								Users can set exit scenarios like "Exit all open positions if total loss goes below a limit", "Exit a specific position if unrealized profit goes more than a limit" or "Exit all positions if total gain moves below a specific percentage of days' maximum variations of gain."						
							</p>
						</div>
				
		
				);
			cards.push(			
			
				<div class={styles.cardDiv} style={styles.featureCard}>
					
					<p style={{ color:'#000'}}>	
							
						<b>SAFTY WITH TRANSPERANCY : </b>Prameyaka can not do any money transaction, but can only trigger trades on behalf of user. Any time user can view the real-time details of their fund and trades triggered by Prameyaka.
					</p>
					
				</div>
			);
			cards.push(
				<div class={styles.cardDiv} style={styles.featureCard}>
					
	
					<p style={{ color:'#000'}}>	
						<b>MODEL TRADER : </b>Prameyaka has a unique concept called Model trader with Rs. 1 lacks of fund allocation. All trades are triggered with platform's settings with user end tuners disabled. 
						Model trader's data will be captured and taken as a reference to tune the algorithms and trading strategies.<br/> Each day, we will publish actual return earned by the Model Trader.				
					</p>
				</div>	   
			);
	}
  
}


