
import React, { Component } from 'react';
import logo_bw from './../images/logo_bw.png';


export default class About extends Component{
	
	constructor( props )
   {
       super(props);
	   this.state ={ pagenm : 0 };
	   
   }
   
	render(){
		
		let padding = this.props.device == 'L' ? '50px' : '1px';
		
		return (
		
			<div class="w3-cell-row " style={{ paddingTop:'40px', paddingLeft:padding, paddingRight:padding}}>
				<header class="w3-container w3-light-gray">
				  <h3>About Prameyaka</h3>
				</header>
				<div className='w3-padding w3-border' >
					<p className='w3-left-align  w3-margin'>

						Prameyaka has initially been build as a standalone application for personal use. The initial version of the application had automated trading for equity delivery which did not turn fruitful. After going through lots of ups and downs with lots of modifications, intraday trading started giving some hope.</p>
					<p  className='w3-left-align w3-margin'>
						Then plan has been made to make it available for multiple users so that the output of all the efforts can be shared with others. So the whole platform has been rebuild with multi-user capabilities, accommodate external trading experts, and a lot more. Prameyaka Technologies is still a very small organization with very limited resources to keep expenses as low as possible to sustain even in non-profitable scenarios.
					</p>
					<div  className='w3-left-align w3-margin'>
						<h5>The platform has been build with the following GOALS.</h5>
						<p className='w3-margin'>
							<li>
								<b>Ultra-simple :</b> Platform has been made as simple as possible with no effort required to trade other than daily sign-in mandated by SEBI as a safety precaution. While building or adding any features we always keep in mind those users who just have basic prerequisites of internet surfing knowledge. But, at the same time, the platform is still attractive for those who have good stock market knowledge.
							</li>
						</p>
						<p className='w3-margin'>
							<li>
								<b>Pay As You Gain :</b> Platform's primary responsibility is to give a good return to its customer's investment. If it can not fulfill its responsibility of giving more than a decent profit then customers won't be charged. We take more credit when the customer gains more and charge less when the customer makes less profit.
							</li>
							
							<span className='w3-small w3-opacity w3-hide'><i>We are planning to make a pricing flat 25% of customer's gain irrespective of loss or gain. In case of loss, 25% of their loss will be added to their account as Prameyaka Compensation Points (PCP) which can be used only for renewal charge of subsequent months. But we are still working on its feasibility and possible legal complexities with such a pricing structure.</i></span>
						</p>
						<p className='w3-margin'>
							<li>
								<b>Hassle-free :</b> There are no formalities for subscription or discontinuance with Prameyaka. Any time you can do trading through Prameyaka just by signing-in and discontinue by a couple of clicks.
							</li>
						</p>
						<p className='w3-margin'>
							<li>
								<b>No Marketing :</b> We don't do marketing knowing the fact that stock market trading involves the risk. We don't like to be the reason for anybody's loss. We don't even motivate/encourage people to use our platform. At the same time, we are proud of being the reason for our customer's gain. We may let people know about the existence of the platform. If somebody wants to know more about the platform, only then we will explain the features of the platform.
							</li>
						</p>
						<p className='w3-margin'>
							<li>
								<b>Transparency :</b> We publish only those data or claims on our website/platform which are true and correct to the best of our knowledge or belief. If you find any discrepancies, please bring it to our notice. We will correct those accordingly.
							</li>
						</p>
						
					</div>
					<div className='w3-border w3-hide-small w3-hide-medium w3-center' style={{height:'380px', opacity:'0.1'}}>
						<img src={logo_bw}/>
					</div>
				</div>
			</div>);
   }
}


