import React, { Component } from 'react';

import logo from './../images/logo_m.png';
import logo_bw from './../images/logo_bw.png';
import "animate.css/animate.min.css";

let fword = ['don\'t','don\'t','don\'t','don\'t','don\'t','don\'t','just', 'just'];
//'<span style=\'letter-spacing:3.5px;\' >just<span>',
//'<span style=\'letter-spacing:3.5px;\'>just<span>'];
let sword = [
'finacial background.','trading skills.','analytical skills.',	'technical skills.','trading strategies.','to spend time.','<i>Prameyaka.</i>','<i>Prameyaka.</i>'	];		
						
const vars = {
	
	lfsize :'25px',
	mfsize :'20px',
	sfsize :'14px',
}
var c= 0;
var timer = setInterval( change, 3500 );

	function change(){
	 
		let div = document.getElementById( "first-word" );
		if( div != null ){
			
			if( c >= 6) div.innerHTML = "<span class='w3-animate-fading'>" + fword[c] + "</span>";
			else div.innerHTML =  fword[c] ;
			document.getElementById( "second-word" ).innerHTML = "<span class='w3-animate-fading' >" + sword[c] + "</span>";
			
		}
		
		if( ++c > 7 ) c = 0;
	}

export default class RollingSentences extends Component{

	constructor(props){
		super(props);
	}	
	
	render(){
		
		
		let ism = 'M' == this.props.device;
		let cFontSize = this.props.device =='S' ? vars.sfsize : (this.props.device =='M' ? vars.mfsize : vars.lfsize );
		
		return (
			<div className ='w3-round w3-text-white'
				style = {{fontFamily: 'Bree Serif', whiteSpace: 'nowrap',position: 'relative', textAlign:'left', letterSpacing:'1.5px', fontSize:cFontSize ,textShadow: '1px 1px 2px #000000', }}>
				<i>
					<span >To become successful <span >trader</span><br/>You&nbsp;&nbsp;							
					<span id="first-word" >just</span>	
					<span >&nbsp;		need </span>
					<span id="second-word">&nbsp;<i>Prameyaka</i></span>
					</span>
				</i>		
			</div>
		);
	}
}


