import React, { Component } from 'react';
import 'w3-css/w3.css';
import logo from './../images/logo_m.png';
import 'font-awesome/css/font-awesome.min.css';

const styles ={
		img :{
			 height: '22px',
		},
		
	
		btnClass : 'w3-button w3-round w3-small w3-white w3-padding-small	',
	};



export default class Header extends Component{
	
	constructor( props ){
		
		super( props );
	}

	render(){

		return(
			<div class='w3-top'>
				<div class="w3-bar w3-border-bottom " style={{backgroundColor:'#fff'}}>				 
					<div class="w3-bar-item w3-left" style={{ alignContent:'left', maxWidth:'40%'}}>
					
						<button className='w3-button w3-round  w3-white w3-padding-small w3-hover-white w3-ripple'
							onClick = {this.home.bind(this)}>
							<img  style={styles.img} src={logo} alt="logo"  />
							
						</button>
					</div>
							
				
					<div className='w3-bar-item  w3-margin-right  w3-right'>
						<div className="w3-dropdown-hover w3-cell w3-margin-right" >
							<button className={styles.btnClass} type="button" id="dropdownMenuButton" >
								<i className="fa fa-bars fa-lg" aria-hidden="true"></i>
							</button>
							<div className="w3-dropdown-content w3-bar-block w3-border w3-border-gray" id ='menumar' >
								<button 
									className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray"
									onClick={this.about.bind(this)}									
									>About
								</button>
								
								<button 
									className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray" 
									onClick={this.getStarted.bind(this)}
									>Get Started
								</button>
								<button 
									className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray" 
									onClick={this.faq.bind(this)}
									>FaQs
								</button>
								<button 
									className="w3-button w3-left-align w3-block w3-small w3-white w3-border w3-border-light-gray" 
									onClick={this.contact.bind(this)}
									>Contact
								</button>
														
							</div>		
								
						</div>	
						<div className='w3-cell w3-margin-left'>
							<button className="w3-button  w3-block w3-white w3-padding-small" >Login
							</button>	
						</div>
					</div>
					
				</div>
			</div>
		);
	}
	
	faq = function(){
		this.props.menuChanged( 'faq' );
	}
	home = function(){
		this.props.menuChanged( 'home' );
	}
	about = function(){
		this.props.menuChanged( 'about' );
	}
	getStarted = function(){
		this.props.menuChanged( 'getstarted' );
	}
	contact = function(){
		this.props.menuChanged( 'contact' );
	}
	
	
	menuChanged = function( event ){

		let source = event.target || event.source;
		
		this.props.menuChanged( source.id );
	}
}

function w3_open() {
  if (mySidebar.style.display === 'block') {
    mySidebar.style.display = 'none';
  } else {
    mySidebar.style.display = 'block';
  }
}
var mySidebar = document.getElementById("mySidebar");

function w3_open() {
  if (mySidebar.style.display === 'block') {
    mySidebar.style.display = 'none';
  } else {
    mySidebar.style.display = 'block';
  }
}

// Close the sidebar with the close button
function w3_close() {
    mySidebar.style.display = "none";
}



