import React, { Component } from 'react';
import 'w3-css/w3.css';
import logo_bw from './../images/logo_bw.png';
const faqs = {
	'1':{
		q : 'Is it safe to login with credentials provided by the broker?',
		a : 'Your broker has security features that will never disclose your credentials to any third-party applications. When you open Prameyaka application, you will be redirected to your broker\'s authentication, where you provide your credentials. Once authenticated, It will be redirected back to us with a token which is valid ONLY for a day. Once you authorize our application, we get the access to manage your portfolio. Our application can not do any money transactions like withdrawal, but has read-only access to the funds available in your trading account, which is required for sizing your positions.'
		
	},
	'2':{
		q : 'Can we set the specific amount to be utilized by the platform?',
		a : 'YES, you can allocate the amount which Prameyaka needs consider for sizing your position . At any point of time, your total margin utilized by the platform will be less than the amount you set. By default, Prameyaka will consider all your available funds if less than maximum amount set by the platform.',

	}	
}

export default class Faq extends Component{
	
	constructor( props ){
		
		super(props );
		this.state = {
			item : '0',
		};
	}


	render(){
		
		let cls = 'w3-left-align';
		let selected = false;
		let padding = this.props.device == 'L' ? '50px' : '1px';
		return (
		<div className='w3-container' style={{maxWidth: '100%', paddingTop:'50px', paddingLeft:padding, paddingRight:padding}}>
			<div class = 'w3-card-4  ' >
				<header class="w3-container ">
				  <h3>Frequenty asked questions</h3>
				</header>
			
				{
					Object.entries(faqs).map(([key,faq])=>{
						
						if(this.state.item == key )
							selected= true;						
						else selected= false;
						
						return(	
							<div class='w3-padding-small w3-border w3-padding-small'>				
								<a href='#' 
									id={key} 
									style={{textDecoration: "none" }}
									onClick={this.onQuestion.bind(this)}
									class=" w3-block  w3-left-align w3-padding-small " >
									<span className='w3-large'>Q.</span> {faq.q}
								</a>					 
								<p class={selected? 'w3-left-align w3-margin-left w3-text-brown' : 'w3-left-align w3-hide'}>
									{faq.a}
									</p>
							</div>				
						
						)
					})					
				}
						
			</div>
			<div className='w3-hide-medium hide-small' style={{height:'660px', opacity:'0.1'}}>
				<img src={logo_bw}/>
			</div>
			</div>
		);
		
	}
	
	onQuestion= function( event ){
		let source = event.target || event.source ;
		this.setState({item : source.id});
	}
	answer = function( no ){
		
	}
}


